import React, { useEffect } from 'react'
import {motion} from 'framer-motion'
// import { Container } from '../styles/globalStyles'
import { FooterContainer, FooterBrand, SocialMedia, FooterSection, FooterWrapper, FooterCopyright } from '../styles/footerStyles'

import logo from '../assets/images/logo/logo.svg'
import fb from '../assets/images/icons/facebook.svg'
import ig from '../assets/images/icons/instagram.svg'
import mg from '../assets/images/icons/messenger.svg'
// import behance from '../assets/images/icons/behance.svg'
import dribbble from '../assets/images/icons/dribbble.svg'

//Scroll Behavior
import {useInView} from 'react-intersection-observer'
import {useAnimation} from 'framer-motion'
import {parent, child} from '../animations/animations'

import {transitionColor, navRoutes} from '../globalVariables'
import AniLink from "gatsby-plugin-transition-link/AniLink"

const Footer = () => {

    const animation = useAnimation();
    const [contentRef, inView] = useInView({
        triggerOnce: true,
        // rootMargin: '-100px',
    })

    useEffect(() => {
        if(inView) {
            animation.start('visible');
        }
    }, [animation, inView])

    return(
        <FooterWrapper>
            <motion.div ref={contentRef} 
                animate={animation} 
                initial='hidden' 
                variants={parent}
            >

                <FooterContainer>
                    <FooterBrand variants={child}>
                        <AniLink 
                            cover
                            direction='right'                                            
                            bg={transitionColor} 
                            to={'/'}>
                            
                            <img src={logo} alt="adsdas" />
                        </AniLink>
                        
                    </FooterBrand>
                    <FooterSection variants={child}>
                        <h2>Navegação</h2>
                        <ul>
                        {navRoutes.map(route => (
                            <li key={route.id}>
                                <AniLink
                                    cover
                                    direction='right'                                            
                                    bg={transitionColor} 
                                    to={`${route.path}`}>
                                    {route.title}
                                </AniLink >
                            </li>
                        ))}
                        </ul>
                    </FooterSection>
                    
                    <FooterSection variants={child}>
                        <SocialMedia>
                            <a href="https://www.facebook.com/pathfinderdesigns"><img src={fb} alt="FB"/></a>
                            <a href="https://www.instagram.com/pathfinderdesigns"><img src={ig} alt="IG"/></a>
                            <a href="https://www.m.me/pathfinderdesigns"><img src={mg} alt="MG"/></a>
                            {/* <Link to="https://www.m.me/pathfinderdesigns"><img src={behance} alt="BH"/></Link> */}
                            <a href="https://dribbble.com/rodrigorocha"><img src={dribbble} alt="DR"/></a>
                            
                        </SocialMedia>
                    </FooterSection>
                    
                </FooterContainer>
                <FooterCopyright variants={child}>
                    <p>Copyright © 2020 Pathfinder Designs. All rights reserved.</p>
                </FooterCopyright>
            </motion.div>
            
        </FooterWrapper>
        
        
    )
}

export default Footer